.Comments {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.Comment {
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	
	margin-top: 15px;
	margin-bottom: 15px;
	line-height: 21px;
	font: normal normal 400 normal 14px "Noto Sans", sans-serif;
	text-align: left;
}
.gilding {
	margin: 0 3px;
}
.gilding img {
	vertical-align: middle;
}
.top-comment {
	margin-top: 40px;
	width: 780px;
	overflow-y: auto;
	height: 425px;
	background-color: rgb(248, 248, 248);
}
.left-box {
	width: 21px;
	margin: 3px 0 0 0;
}
.Comment p {
	padding-bottom: 3.5px;
	padding-left: 0;
	padding-right: 0;
	padding-top: 0;
	margin: 0;
}
.Comment * {
	line-height: 21px;
	word-break: break-word;
}
.controls {
	display: flex;
	justify-content: space-between;
	margin: 25px 0;
}
.controls button {
	font-size: 24px;
	padding: 5px 10px;
}
.ql-editor {
	outline: none;
	white-space: pre-wrap;
}
.ql-editor blockquote {
	border-left: 4px solid rgba(215, 218, 220, 0.2);
	margin: 4px 0px 4px 8px;
	padding-left: 8px;
	quotes: none;
}